import React, { Suspense, lazy } from 'react';
import { Switch, BrowserRouter } from 'react-router-dom';
import paths from './paths';
import ScrollToTop from './scrollToTop';
import Loading from '../Components/UI/Loading';
import DefaultLayout from '../Components/Layouts/DefaultLayout';

const Home = lazy(() => import('../Components/Screens/Home'));
const Security = lazy(() => import('../Components/Screens/Security'));
const Control = lazy(() => import('../Components/Screens/Control'));
const Tracking = lazy(() => import('../Components/Screens/Tracking'));
const Terms = lazy(() => import('../Components/Screens/Terms'));
const Privacy = lazy(() => import('../Components/Screens/Privacy'));
const NotFound = lazy(() => import('../Components/Screens/NotFound'));
const WorkWithUs = lazy(() => import('../Components/Screens/WorkWithUs'));

const Routes = () => {
  return (
    <Suspense fallback={<Loading />}>
      <BrowserRouter basename="/landing">
        <ScrollToTop />
        <Switch>
          <DefaultLayout
            exact
            path={paths.HOME}
            Component={Home}
            solidNavBar={false}
          />
          <DefaultLayout exact path={paths.SECURITY} Component={Security} />
          <DefaultLayout exact path={paths.CONTROL} Component={Control} />
          <DefaultLayout exact path={paths.TRACKING} Component={Tracking} />
          <DefaultLayout exact path={paths.TERMS} Component={Terms} />
          <DefaultLayout exact path={paths.PRIVACY} Component={Privacy} />
          <DefaultLayout
            exact
            path={paths.WORK_WITH_US}
            Component={WorkWithUs}
          />
          <DefaultLayout Component={NotFound} />
        </Switch>
      </BrowserRouter>
    </Suspense>
  );
};

export default Routes;
